.App{
    display: flex;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
    gap: 50px;
    background-color: white;
    color:black;
}
.App form{
    width: 200px;
    display: flex;
    flex-direction: column;
    gap: 15px;
}
input,textarea{
    width: 100%;
    background-color: orange;
    border: 0;
    color: black;
    padding: 5px;
    border-radius: 5px;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 10px;
}
input{
    border-bottom: 1px solid black;
}
.values{
    text-align: center;
    width: 200px;
}
textarea{
    width: 100%;
    border-left: 1px solid black;
    resize: none;
}
input:focus,textarea:focus{
    outline: none;
}